html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Amiko", sans-serif;
  line-height: 1.5;
  background-color: #2f1950;
}
body ::selection {
  background-color: #7353ba;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  text-align: center;
  margin: 2rem;
  line-height: 1.2;
}

h1 {
  font-size: 4rem;
  font-family: "Bebas Neue", cursive;
  color: #7353ba;
}
@media (min-width: 700px) {
  h1 {
    font-size: 4.8rem;
  }
}
@media (min-width: 900px) {
  h1 {
    font-size: 6rem;
  }
}
h1::selection {
  background-color: #faa8ff;
}

h2 {
  font-size: 3.8rem;
  font-family: "Montserrat", sans-serif;
  color: #faa8ff;
}

h3 {
  font-size: 3.4rem;
  color: #efc3f5;
  margin: 1rem;
}

p {
  font-family: "Amiko", sans-serif;
  font-size: 2rem;
  color: #ffff;
  margin: 1rem;
}

a {
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1.6;
  font-weight: bold;
  padding: 1rem;
  transition: 0.2s ease-in-out;
  color: #faa8ff;
}
a:active {
  color: #2f1950;
}
@media (hover: hover) {
  a:hover {
    color: #7353ba;
  }
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  width: 100%;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.nomargin {
  margin: 0;
}

.ocultar {
  display: none;
}

.btn {
  margin: 1rem auto;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  width: 50%;
  border: 4px solid #faa8ff;
  border-radius: 2.5rem;
}
.btn:active {
  background-color: #faa8ff;
}
@media (hover: hover) {
  .btn:hover {
    background-color: #faa8ff;
  }
}
@media (min-width: 900px) {
  .btn {
    width: 30rem;
    margin-bottom: 2rem;
  }
}

@keyframes opening-logo {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.entrance {
  -webkit-animation: scale-in-tr 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: scale-in-tr 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  transition: all ease 0.5s;
}

