}
.menu {
  display: none;
}
@media (min-width: 900px) {
  .menu {
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    margin: 10rem auto 0 auto;
    width: 50%;
    align-items: center;
  }
}
@media (min-width: 900px) {
  .menu a {
    font-family: "Amiko", sans-serif;
    font-size: 2.7rem;
    animation-name: opening-logo;
    animation-duration: 1.6s;
    animation-timing-function: ease-in-out;
  }
}
.menu .logo img {
  text-align: center;
  width: 8rem;
  animation-name: opening-logo;
  animation-duration: 1.6s;
  animation-timing-function: ease-in-out;
}

.menu-mobile {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2f1950;
  top: 0;
  width: 100%;
  height: 7rem;
}
@media (min-width: 900px) {
  .menu-mobile {
    display: none;
  }
}

.icon .logo img {
  display: block;
  width: 5rem;
  height: 9rem;
  margin: 1rem;
  animation-name: opening-logo;
  animation-duration: 1.6s;
  animation-timing-function: ease-in-out;
}

.nav {
  position: absolute;
  width: 100%;
  top: 6rem;
  background-color: #2f1950;
  z-index: 1;
}
.nav a {
  display: block;
  font-size: 2rem;
  margin: 0;
  padding: 0;
  text-align: center;
  display: block;
  padding: 1.5rem 0;
  color: #efc3f5;
}
.nav a:active {
  color: #2f1950;
  background-color: #efc3f5;
}

.hamburguer a svg {
  display: block;
  margin: 1rem;
  fill: #faa8ff;
  width: 4rem;
  height: 9rem;
}

.main-img {
  background: url(../assets/img/main-image.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  text-align: center;
  margin-top: 10rem;
  margin-bottom: 6rem;
  height: 20rem;
  width: 100%;
  transform-origin: 0 0;
  transform: skewY(-10deg);
  overflow: hidden;
}
@media (min-width: 700px) {
  .main-img {
    margin-top: 12.5rem;
    margin-bottom: 3rem;
  }
}
@media (min-width: 900px) {
  .main-img {
    margin-top: 20rem;
    height: 40rem;
  }
}
.main-img__bg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
}
@media (min-width: 700px) {
  .main-img__bg {
    height: 100%;
  }
}
.main-img__text {
  transform: skewY(10deg);
}
@media (min-width: 900px) {
  .main-img__text {
    margin-top: 0;
  }
}
.main-img__text p {
  color: #faa8ff;
  font-weight: bold;
}
@media (min-width: 900px) {
  .main-img__text p {
    font-size: 2.5rem;
  }
}

.intro {
  display: grid;
  font-size: 2rem;
  margin: 4rem auto;
  margin-top: -5rem;
  width: 100%;
}
.intro h2 {
  margin-bottom: 0;
}
.intro__text {
  text-align: center;
}
.intro__text p {
  margin-top: 1rem;
}

.work__title {
  margin-bottom: 5rem;
}
@media (min-width: 700px) {
  .work__title {
    margin-bottom: 10rem;
  }
}
@media (min-width: 900px) {
  .work__title {
    margin-bottom: 13rem;
  }
}
.work__items {
  padding-top: 2rem;
  transform-origin: 0 0;
  transform: skewY(-10deg);
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
}
@media (min-width: 700px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}
@media (min-width: 900px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0;
  }
}
.grid__item {
  padding: 0;
  position: relative;
  overflow: hidden;
  height: 15rem;
}
@media (min-width: 700px) {
  .grid__item {
    height: 12rem;
  }
}
@media (min-width: 900px) {
  .grid__item {
    height: 15rem;
  }
}
.grid__item img {
  width: 101%;
  margin-top: -6rem;
  transform: skewY(10deg) scale(1, 1);
  transition: all 0.75s;
}
.grid__item:hover img {
  transform: skewY(10deg) scale(1.4, 1.4);
}

.grid__item .bg-hover {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 150%;
  display: grid;
  position: absolute;
  top: -25%;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  transform: skewY(10deg) scale(1, 1);
  justify-items: center;
  align-items: center;
  text-align: center;
}
.grid__item .bg-hover .info__btn {
  margin: 0.5rem;
  font-size: 1.8rem;
}
.grid__item:hover .bg-hover {
  opacity: 1;
  overflow: hidden;
}
.grid__item:hover .bg-hover .info {
  animation-name: bounce;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}

.skills {
  text-align: center;
  margin: 0 auto;
  padding-bottom: 15rem;
}
@media (min-width: 700px) {
  .skills {
    width: 70%;
  }
}
.skills h3 {
  color: #ffff;
}
.skills__content {
  display: grid;
}
@media (min-width: 700px) {
  .skills__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 900px) {
  .skills__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
.skills__skill {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skills__skill i {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #faa8ff;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  font-size: 4rem;
  color: #2f1950;
  box-shadow: 1rem 1rem 1.6rem -6px rgba(0, 0, 0, 0.45);
}
.skills__skill h3 {
  color: #efc3f5;
  font-size: 2.8rem;
  margin: 1rem;
}
@media (min-width: 900px) {
  .skills__skill h3 {
    font-size: 3rem;
  }
}

.contact {
  background-color: #000;
  text-transform: uppercase;
  padding-top: 15rem;
  color: #ffff;
  transform: skewY(-10deg);
}
.contact__columns div {
  width: 100%;
  text-align: center;
}
@media (min-width: 900px) {
  .contact__columns {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
}

.contact__container {
  transform: skewY(10deg);
  color: #ffff;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 900px) {
  .contact__container {
    width: 110rem;
    margin: 0 auto;
    padding: 0;
  }
}
@media (min-width: 900px) {
  .contact__container .contact__location {
    text-align: left;
  }
}
@media (min-width: 900px) {
  .contact__container .contact__location h3 {
    text-align: left;
  }
}
@media (min-width: 900px) {
  .contact__container .contact__rights h3,
.contact__container .contact__rights p {
    text-align: right;
  }
  .contact__container .contact__rights p a {
    padding-right: 0;
  }
}

.contact__social {
  margin-top: 2rem;
}
.contact__social a svg {
  width: 4rem;
  fill: #faa8ff;
}
.contact__social a:hover path {
  transition: all ease 0.5s;
  fill: #7353ba;
}

.footer {
  background-color: #000;
  height: 45rem;
  margin-top: -17rem;
}